import React from "react"
import { graphql } from "gatsby"
import styled from "styled-components"
import Layout from "../components/layout"
import SEO from "../components/seo"
import PostMetaItem from "../components/post-meta-item"
import TagItem from "../components/tag-item"
import "katex/dist/katex.min.css"

const PostTitle = styled.h2`
  margin: 0;
  padding: 0 0 1rem 0;
`

const PostSection = styled.section`
  margin: 0;
  padding: 0 0 2rem 0;

  & a:hover {
    color: #ffcc06;
    text-decoration: underline;
  }
`

const PostMetaList = styled.div`
  margin: 0;
  padding: 0;
  color: #333;
  font-size: 0.8rem;
`

const PostTagList = styled.div`
  margin: 0;
  padding: 0;
`

export default ({ data }) => {
  const post = data.markdownRemark
  const timeToRead = `${post.timeToRead} min`
  return (
    <Layout>
      <SEO title={post.frontmatter.title} />
      <PostTitle>{post.frontmatter.title}</PostTitle>
      <PostMetaList>
        <PostMetaItem icon="🗓" iconLabel="Date" text={post.frontmatter.date} />
        {` · `}
        <PostMetaItem icon="⏱" iconLabel="timeToRead" text={timeToRead} />
      </PostMetaList>
      <PostSection dangerouslySetInnerHTML={{ __html: post.html }} />
      <PostTagList>
        {post.frontmatter.tags.map(tag => (
          <TagItem key={tag} name={tag} />
        ))}
      </PostTagList>
    </Layout>
  )
}

export const query = graphql`
  query($slug: String!) {
    markdownRemark(fields: { slug: { eq: $slug } }) {
      html
      frontmatter {
        title
        date(formatString: "YYYY/MM/DD")
        tags
      }
      timeToRead
    }
  }
`
